@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100%}.image img{width:100%;height:100%}.footer{width:100%}.MainPage{display:flex;flex-direction:column;padding:0;overflow:hidden}.mainimgbg{position:relative;background-color:aquamarine;height:100vh;width:100%}.mainimgbg .name{padding-left:55px;padding-right:55px}.mainimgbg .photo{position:absolute;bottom:0px;left:38.75%;max-width:428px;max-height:1269px;width:22.3%}.description{color:#d0b5b5;font-family:"Bebas Neue";font-size:180px;font-weight:400;position:absolute;bottom:50px}@media (min-width: 1200px){.mainimgbg{height:75vw}}@media (min-width: 1600px){.description{font-size:237px}}

